.hero__popup {
  display: none;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 2;
  &.active {
    display: block;
  }
  &-container {
    display: flex;
    width: 100%;
    height: 100%;
    padding: 0 15px;
  }
  &-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 32px;
    margin: auto;
    width: 631px;
    max-width: 100%;
    background-color: var(--light-color);
    border-radius: 16px;
    padding: 44px 0px;
    position: relative;
  }
  &-text {
    width: 408px;
    max-width: 100%;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 36px;
    text-align: center;
    color: var(--dark-color);
    @media (max-width: 380px) {
      font-size: 26px;
      line-height: 30px;
    }
  }
  &-form {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    padding: 0 15px;
    text-align: left;
  }

  &-input {
    width: 440px;
    max-width: 100%;
    height: 44px;
    padding-left: 12px;
    background: var(--light-color);
    border: 1px solid #d1d5db;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 24px;
    &:not(:last-child) {
      margin-bottom: 24px;
    }
    &:focus {
      outline: none;
    }
  }
  &-label {
    display: block;
    margin-bottom: 4px;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 20px;
    color: #374151;
  }
  &-buttons {
    @media (max-width: 370px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 10px;
    }
  }
  &-sign--in {
    width: 128px;
    max-width: 100%;
    background-color: var(--yellow-color);
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: var(--dark-color);
  }
  &-log--in {
    width: 218px;
    max-width: 100%;
    border: 1px solid var(--yellow-color);
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: #374151;
  }
  &-close {
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 21px;
    cursor: pointer;
  }
}
