.swiper {
  // width: 100%;
  height: 100%;
  padding: 10px;
  &-wrapper {
    margin: 0 auto;
  }
  &-slide {
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    width: 300px;
  }
  &-slide-active {
    width: 300px;
    max-width: 100%;
  }
  &-slide .branch__item {
    width: 300px;
  }
  &-button-next, &-button-prev {
    display: none;
  }
}
.branch__item-link, .swiper-slide, .swiper-slide-duplicate, .swiper-slide-active {
  width: 300px;
}
