.header__inner {
  position: relative;
}
.dropdown {
  position: absolute;
  right: 0;
  top: 88px;
  width: 290px;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0px;
  background: var(--light-color);
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.1), 0px 4px 6px -2px rgba(0, 0, 0, 0.05),
    0px 0px 0px 1px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
  z-index: 4;
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  &.active {
    opacity: 1;
    visibility: visible;

  }
  &__list {
    padding: 16px 0px;
    padding-left: 16px;
  }
  &__item:not(:last-child) {
    padding-bottom: 16px;
  }
  &__link {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: var(--dark-color);
  }
}
