.hero__nav {
  position: relative;
}

.hero__dropdown {
  position: absolute;
  left: 0;
  margin-top: 10px;
  &-list {
    display: flex;
    align-items: center;
    gap: 32px;
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  }
  &-list.active {
    opacity: 1;
    visibility: visible;
  }
  &-item:hover {
    text-decoration: underline;
  }
}
