.features {
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 32px;
    margin-bottom: 124px;
    @media (max-width: 936px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
  &__left {
    width: 743px;
    max-width: 100%;
    background-color: var(--light-color);
    border-radius: 16px;
    @media (max-width:1060px) {
      width: 570px;
    }
  }
  &__left-inner {
    width: 683px;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    gap: 16px;
    justify-content: center;
    align-items: center;
    padding: 60px 10px;
  }
  &__left-item {
    text-align: left;
    width: 500px;
    max-width: 100%;
  }
  &__left-title {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 46px;
    color: var(--dark-color);
    width: 522px;
    max-width: 100%;
    margin-bottom: 20px;
    @media (max-width: 546px) {
      font-size: 30px;
      line-height: 30px;
      text-align: center;
    }
  }
  &__left-subtitle {
    display: flex;
    align-items: center;
    margin-bottom: 12px;
    gap: 10px;
    @media (max-width: 400px) {
      margin-bottom: 10px;
    }
  }
  &__left-subtitle--text {
    @media (max-width: 400px) {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
    }
  }
  &__left-text {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: var(--dark-color);
    @media (max-width: 400px) {
      font-size: 16px;
      line-height: 26px;
    }
  }
  &__right {
    width: 521px;
    max-width: 100%;
    background-color: var(--yellow-color);
    border-radius: 16px;
    position: relative;
    @media (max-width: 936px) {
      height: 600px;
    }
  }
  &__right-inner {
    text-align: center;
  }
  &__right-title {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 49px;
    color: var(--dark-color);
    text-align: center;
    padding: 58px 5px 0px 5px;
    margin-bottom: 20px;
    @media (max-width: 425px) {
      font-size: 30px;
    }
    @media (max-width: 350px) {
      font-size: 26px;
    }
  }
  &__right-price {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 46px;
    margin-bottom: 8px;
    @media (max-width: 425px) {
      font-size: 30px;
    }
  }
  &__right-text {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    display: flex;
    text-align: center;
    justify-content: center;
    color: #f50f22;
    margin-bottom: 56px;
    margin-top: 30px;
    @media (max-width: 425px) {
      margin-top: 20px;
      margin-bottom: 30px;
    }
  }
  &__btn {
    background-color: var(--light-color);
    padding: 12px 32px;
    width: 230px;
    max-width: 100%;
  }
  &__number {
    max-width: 100%;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 700;
    font-size: 216px;
    line-height: 45px;
    letter-spacing: -0.03em;
    color: #f2f2f2;
    position: absolute;
    bottom: 58px;
    right: 0;
    @media (max-width: 1161px) {
      font-size: 160px;
      bottom: 35px;
    }
    @media (max-width: 350px) {
      font-size: 120px;
      bottom: 22px;
    }
  }
}
