.header {

  &__logo {
    @media (max-width: 965px) {
      display: none;
    }
  }
  &__logo-min {
    display: none;
    @media (max-width:965px) {
      display: block;
    }

  }
  &__inner {
    padding-top: 28px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #e8e8e8;
    padding-bottom: 20px;
    // overflow-x: hidden;
    @media (max-width: 1180px) {
      gap: 15px;
    }
  }
  &__nav {
    @media (max-width: 830px) {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 2;
      padding: 30px;
      background-color: var(--light-color);
      transform: translateX(100%);
      transition: .3s ease-in-out;
    }
  }
  &__nav-visible {
    transform: none;
  }
  &__nav-list {
    display: flex;
    align-items: center;
    gap: 32px;
    @media (max-width: 830px) {
      flex-direction: column;
    }
  }
  &__nav-list--link {
    transition: 0.3s ease;
    &:hover {
      text-decoration: underline;
    }
  }
  &__searchbar {
    @media (max-width: 830px) {
      width: 100%;
    }

  }
  &__searchbar-text {
    width: 320px;
    max-width: 100%;
    height: 36px;
    padding: 8px 12px;
    border: 1px solid #d1d5db;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 36px;
    &::placeholder {
      font-family: "Calibri";
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: #6b7280;
      padding: 12px;
    }
    @media (max-width: 1180px) {
      width: 250px;
    }
    @media (max-width: 830px) {
      width: 100%;
    }
  }
  &__acc {
    display: flex;
    align-items: center;
    gap: 12px;
    @media (max-width: 830px) {
      display: none;
    }
  }
  &__acc-round {
    background-color: var(--indigo-color);
    width: 40px;
    height: 40px;
    border-radius: 20px;
    padding: 13px;
    padding-top: 10px;
    border: none;
    cursor: pointer;
  }
}
