.hero {
  &__inner {
  }
  &__nav-list {
    display: flex;
    align-items: center;
    gap: 32px;
    @media(max-width: 767px) {
      display: none;
    }
  }
  &__nav-link:hover {
    text-decoration: underline;
  }
  &__nav-more:hover {
    text-decoration: none;
  }
  &__block {
    margin-top: 130px;
    display: flex;
    align-items: center;
    @media (max-width: 684px) {
      flex-direction: column;
      justify-content: center;
    }
  }
  &__block-text {
    width: 745px;
    max-width: 100%;
    @media (max-width: 684px) {
      order: 2;
      margin-top: 30px;
      text-align: center;
    }
  }
  &__block-title--block {
    max-width: 700px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    @media (max-width:925px) {
      gap: 0;
    }
    @media (max-width: 684px) {
      align-items: center;
    }
  }
  &__block-title--block span {
    width: 360px;
    max-width: 100%;
    height: 60px;
    background-color: var(--yellow-color);
    padding: 0 30px;
    border-radius: 12px;
    line-height: 45px;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 60px;
    color: #111827;
    @media (max-width: 1123px) {
      width: 260px;
      font-size: 40px;
    }
    @media (max-width: 925px) {
      width: 240px;
      font-size: 34px;
      height: 40px;
      line-height: 33px;
    }
    @media (max-width: 394px) {
      font-size: 26px;
      width: 200px;
    }
  }
  &__block-title {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
    color: #111827;
    position: relative;

    @media (max-width: 1123px) {
      font-size: 45px;
    }
    @media (max-width: 925px) {
      font-size: 34px;
    }
    @media (max-width: 684px) {
      text-align: center;
    }
    @media (max-width: 394px) {
      font-size: 26px;
    }
  }
  &__block-subtitle {
    max-width: 522px;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 28px;
    line-height: 36px;
    color: var(--grey-color);
    margin-top: 30px;
    margin-bottom: 30px;
    @media (max-width: 1180px) {
      margin-top: 30px;

    }
    @media (max-width: 1123px) {
      margin-top: 20px;
    };
    @media (max-width: 925px) {
      margin-top: 20px;
      margin-bottom: 35px;
      font-size: 22px;
      line-height: 28px;
    }
    @media (max-width: 394px) {
      margin-top: 10px;
      margin-bottom: 15px;
      font-size: 21px;
    }
  }
  &__buttons {
    @media (max-width: 415px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 10px;
    }
  }
  &__button-1 {
    background-color: var(--yellow-color);
    margin-right: 20px;
    @media (max-width: 925px) {
      width: 184px;
      margin-right: 0;
    }
    @media (max-width: 415px) {
      width: 272px;
    }
  }
  &__button-2 {
    width: 177px;
    max-width: 100%;
    border: 1px solid var(--yellow-color);
    @media (max-width: 415px) {
      width: 272px;
    }
  }
  &__nav-more {
    display: flex;
    align-items: center;
    gap: 5px;
    position: relative;
    &.active::after {
      transform: rotate(-45deg);
      top: 7px;
    }
  }
  &__nav-more::after {
    position: absolute;
    right: -15px;
    top: 3px;
    content: "";
    width: 9px;
    height: 9px;
    border-top: 2px solid #374151;
    border-right: 2px solid #374151;
    display: block;
    transform: rotate(135deg);
  }

}
