.popup {
  display: none;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.6);
  &.active {
    display: block;
  }
  &__container {
    display: flex;
    width: 100%;
    height: 100%;
  }
  &__body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 32px;
    margin: auto;
    width: 631px;
    max-width: 100%;
    background-color: var(--light-color);
    border-radius: 16px;
    padding: 44px 0px;
    position: relative;
  }
  &__text {
    width: 408px;
    max-width: 100%;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 36px;
    text-align: center;
    color: var(--dark-color);
  }
  &__btn {
    background-color: var(--yellow-color);
    border-radius: 26px;
    width: 127px;
    max-width: 100%;
    padding: 12px 32px;
  }
}
