.footer {
  background-color: var(--light-color);
  &__inner {
    display: flex;
    gap: 32px;
    padding-top: 44px;
    max-width: 100%;
    @media (max-width: 470px) {
      flex-direction: column;
      gap: 20px;
    }
  }
  &__row {
    width: 300px;
    max-width: 100%;
    padding-bottom: 56px;
    @media (max-width: 470px) {
      flex-direction: column;
      padding-bottom: 10px;
    }
  }
  &__row-title {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: var(--dark-color);
    margin-bottom: 16px;
  }
  &__nav-item {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: var(--dark-color);
  }
  &__nav-item:not(:last-child) {
    margin-bottom: 12px;
  }
  &__nav-link {
    transition: all 0.3s ease;
    &:hover {
      text-decoration: underline;
    }
  }
  &__under {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid grey;
    padding-top: 32px;
    padding-bottom: 44px;
    @media (max-width: 400px) {
      flex-direction: column;
    }
  }
  &__under-right {
    @media (max-width: 400px) {
      order: 1;
    }
  }
  &__under-left {
    @media (max-width: 400px) {
      order: 2;
    }
  }
  &__under-left,
  &__under-right {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--dark-color);
  }
}
