.burger {
  position: relative;
  width: 30px;
  height: 20px;
  display: none;
  border: none;
  cursor: pointer;
  padding: 0;
  background: transparent;
  z-index: 3;
  @media (max-width: 830px) {
    display: block;
  }
  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 60%;
    height: 2px;
    background-color: var(--dark-color);
    transition: opacity .3s ease-in-out;
  }
  &::before, &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: var(--dark-color);
    transition: transform .3s ease-in-out, top .3s ease-in-out, bottom .3s ease-in-out;
  }
  &::before {
    top: 0;
  }
  &::after {
    bottom: 0;
  }
  &-active .burger__line {
    opacity: 0;
    visibility: hidden;
  }
  &-active::before {
    top: 50%;
    transform: rotate(45deg);
    background-color: #9CA3AF;
  }
  &-active::after {
    bottom: auto;
    top: 50%;
    transform: rotate(-45deg);
    background-color: #9CA3AF;
  }
}

.stop-scroll {
  overflow: hidden;
}
