// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Open Sans", sans-serif;
  --content-width: 1296px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --yellow-color: #f1df6f;
  --indigo-color: #c7d2fe;
  --grey-color: #626262;
  --dark-color: #111827;
}
