.sidebar {
  &__container {
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 32px;
    align-items: flex-start;
    margin-bottom: 100px;
    @media (max-width: 992px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__inner-right {
    display: flex;
    flex-direction: column;
    position: static;
    align-items: flex-end;
    width: 411px;
    max-width: 100%;
    overflow-y: hidden;
    @media (max-width: 992px) {
      width: 100%;
      align-items: center;
    }
  }
  &__inner-left {
    width: 853px;
    max-width: 100%;
    @media (max-width: 1230px) {
      width: 750px;
    }
    @media (max-width: 1125px) {
      width: 620px;
    }
    @media (max-width: 992px) {
      order: 2;
    }
  }
  &__btn-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 16px;
    width: 411px;
    height: 292px;
    max-width: 100%;
    background-color: var(--light-color);
    border-radius: 12px;
    padding: 58px 40px;
  }
  &__btn {
    width: 331px;
    max-width: 100%;
    border: 1px solid var(--yellow-color);
  }
  &__btn-yellow {
    background-color: var(--yellow-color);
  }
  &__portfolio {
    width: 411px;
    max-width: 100%;
    height: auto;
    padding: 28px 40px;
    background-color: var(--light-color);
    border-radius: 12px;
    margin: 32px 0px;
  }
  &__portfolio-title {
    margin-bottom: 10px;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    color: var(--dark-color);
  }
  &__portfolio-subtitle {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: #f50f22;
  }
  &__examples {
    width: 411px;
    max-width: 100%;
    padding: 28px 40px;
    background-color: var(--light-color);
    border-radius: 12px;
    margin-bottom: 32px;
  }
  &__examples-title {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    color: var(--dark-color);
    margin-bottom: 10px;
  }
  &__examples-subtitle {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: #f50f22;
    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }
  &__contacts {
    width: 411px;
    max-width: 100%;
    padding: 28px;
    background-color: var(--light-color);
    border-radius: 12px;
    margin-bottom: 32px;
    @media (max-width: 992px) {
      order: -1;
    }
  }
  &__contacts-block:not(:last-child) {
    margin-bottom: 40px;
  }
  &__contacts-block--title {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    color: var(--dark-color);
    margin-bottom: 4px;
  }
  &__contacts-text {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__contacts-block--subtitle {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
  }
  &__contacts-block--subtitle-right {
    text-align: right;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    color: var(--dark-color);
  }
  &__profile {
    width: 853px;
    max-width: 100%;
    background-color: var(--light-color);
    border-radius: 12px;
    padding: 28px 36px 36px 36px;
    display: flex;
  }
  &__profile-del {
    @media (max-width: 992px) {
      display: none;
    }
  }
  &__profile-text {
    width: 100%;
  }
  &__profile-img {
    margin-right: 16px;
  }
  &__profile-title {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 12px;
  }
  &__profile-age {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: var(--dark-color);
    margin-bottom: 4px;
  }
  &__profile-job {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: var(--dark-color);
    margin-bottom: 12px;
  }
  &__profile-location {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
  &__profile-past {
    width: 395px;
    max-width: 100%;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: var(--dark-color);
  }
  &__profile-under {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__profile-tel {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: #9ca3af;
  }
  &__left-btn {
    background-color: var(--yellow-color);
  }
  &__reason,
  &__info {
    width: 853px;
    max-width: 100%;
    background-color: var(--light-color);
    border-radius: 12px;
    padding: 44px 36px;
    margin: 32px 0px;
  }
  &__reason-title,
  &__info-title {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 28px;
    color: var(--dark-color);
    margin-bottom: 12px;
  }
  &__reason-subtitle,
  &__info-subtitle {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: var(--dark-color);
    padding-right: 78px;
  }
  &__info-subtitle {
    margin-bottom: 20px;
  }
  &__info-link {
    margin-top: 20px;
    &:hover {
      text-decoration: underline;
    }
  }
  &__list {
    width: 703px;
    max-width: 100%;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: var(--dark-color);
  }
  &__profile-min {
    display: none;
    @media (max-width: 992px) {
      display: block;
      width: 411px;
      max-width: 100%;
      order: -1;
      margin-bottom: 32px;
    }
  }
  &__profile-min--under {
    align-items: flex-start;
    margin-top: 16px;
    flex-direction: column;
    gap: 32px;
  }
  &__profile-min--btn {
    width: 100%;
  }
  &__profile-min--title {
    margin-top: 10px;
  }
  &__acc-btn {
    width: 100%;
    height: 100%;
    text-align: left;
    border: none;
    background: none;
    padding: 0;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 28px;
    color: var(--dark-color);
    position: relative;
    &.active::after {
      transform: rotate(-45deg);
    }
  }
  &__acc-btn::after {
    position: absolute;
    right: 0;
    top: 6px;
    content: "";
    width: 9px;
    height: 9px;
    border-top: 2px solid #374151;
    border-right: 2px solid #374151;
    display: block;
    transform: rotate(135deg);
  }
  &__acc {
    display: none;
    &.active {
      display: block;
    }
  }
  &__reason-title, &__info-title {
    &.active {
      display: none;
    }
  }
}
