.branch {
  margin-bottom: 124px;
  &__title {
    margin-top: 50px;
    margin-bottom: 24px;
    text-align: center;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 46px;
    color: #111827;
    @media (max-width: 600px) {
      font-size: 30px;
    }
  }
  &__inner {
    display: flex;
    gap: 32px;
    margin-bottom: 30px;
  }

  &__item-link {
    width: 300px;
  }
  &__item {
    width: 300px;
    max-width: 100%;
    height: 100px;
    border: 1px solid #e0e0e0;
    border-radius: 12px;
    padding-left: 20px;
    display: flex;
    align-items: center;
    background-color: var(--light-color);
    position: relative;
    margin: 0;
    @media (max-width: 400px) {
      height: 70px;
      padding-left: 5px;
    }
  }
  &__item-img {
    background-color: var(--yellow-color);
    padding: 8px;
    border-radius: 12px;
    @media (max-width: 460px) {
      width: 25px;
      height: 25px;
      padding: 4px;
    }
  }
  &__item-text {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: #111827;
    margin-left: 24px;
    @media (max-width: 460px) {
      font-size: 20px;
      margin-left: 5px;
    }
    @media (max-width: 460px) {
      font-size: 14px;
      line-height: 14px;
    }
  }
  &__round {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 40px;
    @media (max-width: 550px) {
      display: none;
    }
  }
  &__round-text {
    border: 1px solid var(--yellow-color);
    border-radius: 50%;
    padding: 10px;
  }
}
