.form {
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 102px;
    max-width: 100%;
    @media (max-width: 936px) {
      flex-direction: column;
    }
  }
  &__block {
    width: 743px;
    max-width: 100%;
    background-color: var(--light-color);
    border-radius: 16px;
    @media (max-width: 936px) {
      width: 540px;
    }
  }
  &__block-title {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 38px;
    padding-top: 32px;
    margin-bottom: 8px;
    text-align: center;
    color: var(--dark-color);
    @media (max-width: 1130px) {
      padding-top: 20px;
    }
    @media (max-width: 490px) {
      font-size: 26px;
    }
  }
  &__block-subtitle {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    color: var(--dark-color);
    margin-bottom: 32px;
    @media (max-width: 1130px) {
      margin-bottom: 20px;
    }
    @media (max-width: 1110px) {
      font-size: 20px;
    }
    @media (max-width: 490px) {
      font-size: 18px;
    }
  }
  &__block-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
    text-align: center;
    border-bottom: 1px solid var(--dark-color);
  }
  &__block-form-input {
    width: 90%;
    height: 44px;
    padding-left: 12px;
    background: var(--light-color);
    border: 1px solid #d1d5db;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 24px;
  }
  &__btn-block {
    text-align: center;
  }
  &__btn {
    margin-top: 30px;
    margin-bottom: 44px;
    width: 90%;
    background-color: var(--yellow-color);
    @media (max-width: 1130px) {
      margin-top: 10px;
      margin-bottom: 30px;
    }
  }
  &__under-text {
    padding: 24px 0px;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: #6b7280;
    text-align: center;
  }
  &__under-text span {
    color: var(--dark-color);
  }
}
