.database {
  &__title {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 46px;
    text-align: center;
    color: var(--dark-color);
    margin-bottom: 24px;
  }
  &__inner {
    display: flex;
    // align-items: center;
    // justify-content: space-between;
    // gap: 20px;
    margin-bottom: 124px;
  }
  &__item {
    width: 300px;
    max-width: 100%;
    background-color: var(--light-color);
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 28px 0px;
  }
  &__time {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 22px;
  }
  &__item--part-time {
    width: 86px;
    max-width: 100%;
    background-color: #fef3c7;
    padding: 2px 12px;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #92400e;
    border-radius: 12px;
  }
  &__item--full-time {
    width: 86px;
    max-width: 100%;
    background-color: #ede9fe;
    padding: 2px 12px;
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #5b21b6;
    border-radius: 12px;
  }
  &__img {
    margin-bottom: 20px;
  }
  &__item-name {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 4px;
  }
  &__item-job {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #979797;
    margin-bottom: 20px;
  }
  &__item-exp,
  &__item-salary {
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: var(--dark-color);
  }
  &__item-salary {
    margin-bottom: 20px;
  }
  &__btn {
    background-color: var(--yellow-color);
    font-family: "Calibri";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: var(--dark-color);
  }
}
